.one-task-container{
    display: flex;
    flex-direction: row;
    justify-content: center;   
    align-items: center; 
    background-color: rgb(97, 195, 222);    
    margin-left: 25%;
    margin-right: 25%;
    height: 50px;
    margin-top: 10px;
    margin-bottom: 10px;
    color: white;
    font-weight: 700;
    font-size: 18px;
}
.task-name{
    margin-left: 10px;
    flex: 1;
    max-width: 200px;
}
.job-name{
    flex: 1;
    max-width: 200px;
}
.task-description{
    flex: 1;
    max-width: 400px;
}
.checkbox{
    flex: 1;
    max-width: 200px;
}